<template>
  <form class="form" method="POST" action="/register" @submit="checkForm">
    <input type="hidden" name="_token" :value="this.$root.token">
    <h1>Register</h1>

    <div class="field">
      <label class="label" for="username">Username</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['username'] }" id="username" ref="username" name="username" type="text" placeholder="Username" v-model.trim="user.username" @focus="clearError('username')" required>
        <span class="icon is-small is-left">
          <i class="fas fa-user"></i>
        </span>
      </div>  
        <p v-if="formErrors.username" class="help is-danger">{{ formErrors.username }}</p>
    </div>

    <div class="field">
      <label class="label" for="email">E-Mail</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['email'] }"  id="email" name="email" type="email" placeholder="Email" v-model.trim="user.email" @focus="clearError('email')" required>
        <span class="icon is-small is-left">
          <i class="fas fa-envelope"></i>
        </span>
      </div>  
        <p v-if="formErrors.email" class="help is-danger">{{ formErrors.email }}</p>
    </div>

    <div class="field">
      <label class="label" for="password">Password</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['password'] }"  id="password" name="password" type="password" v-model.trim="user.password" placeholder="Password" @focus="clearError('password')" required>
        <span class="icon is-small is-left">
          <i class="fas fa-lock"></i>
        </span>
      </div>
        <p v-if="formErrors.password" class="help is-danger">{{ formErrors.password }}</p>
    </div>

    <div class="field">
      <label class="label" for="password-confirm">Confirm Password</label>
      <div class="control has-icons-left">
        <input class="input"  id="password-confirm" name="password_confirmation" type="password" v-model.trim="user.password_confirm" placeholder="Confirm Password" @focus="clearError('password')" required>
        <span class="icon is-small is-left">
          <i class="fas fa-lock"></i>
        </span>
      </div>
    </div>

    <div class="field">
      <div class="control">
        <button class="button is-primary" type="submit">Register →</button>
      </div>
    </div>
  </form>
</template>

<script>
import { deleteErrors } from "../mixins";

export default {
  name: "Register",
  props: ["old", "errors"],
  mixins: [deleteErrors],
  data() {
    return {
      user: {
        username: "",
        email: "",
        password: "",
        password_confirm: ""
      },
      formErrors: {}
    };
  },
  methods: {
    checkForm(e) {
      Object.keys(this.user).map(key => {
        if (this.user[key] == "") {
          this.$set(this.formErrors, key, "This field is required");
        }
      });

      if (this.user.password !== this.user.password_confirm) {
        this.$set(this.formErrors, "password", "The passwords do not match");
      }

      if (this.user.password.length < 6) {
        this.$set(
          this.formErrors,
          "password",
          "The password must be at least 7 characters."
        );
      }

      if (this.user.username.length < 6) {
        this.$set(
          this.formErrors,
          "username",
          "The username must be at least 6 characters"
        );
      }
      const emailRegEx = /\S+@\S+\.\S+/;
      if (this.user.email.length < 6 || !this.checkEmail(this.user.email)) {
        this.$set(
          this.formErrors,
          "email",
          "Please enter a valid email address"
        );
      }

      if (Object.keys(this.formErrors).length > 0) {
        return e.preventDefault();
      }
    },
    checkEmail(email) {
      const emailRegEx = /\S+@\S+\.\S+/;
      return emailRegEx.test(email);
    }
  },
  mounted() {
    this.$nextTick(() => {
      if (
        !Array.isArray(this.old) &&
        Object.getOwnPropertyNames(this.old).length > 0
      ) {
        this.user = Object.assign({}, this.user, this.old);
      }

      if (this.errors.length) {
        //let errObj = {};
        this.errors.map(err => {
          let name = err.split(" ")[1];
          // errObj[name] = err;
          this.$set(this.formErrors, name, err);
        });
        //this.formErrors = Object.assign({}, this.formErrors, errObj);
      }

      this.$refs.username.focus();
    });
  }
};
</script>




