exports.deleteErrors = {
  methods: {
    clearError(prop) {
      if (prop in this.formErrors) {
        delete this.formErrors[prop];
      }
    }
  }
};

exports.commentErrors = {
  methods: {
    updateRating(rating) {
      this.clearErrors();
      this.rating = rating;
    },
    clearErrors() {
      this.commentErrs.splice(0);
    }
  }
};

exports.updateImage = {
  methods: {
    updateImg() {
      let input = this.$refs.image;
      this.clearError("image");
      // check if correct format
      if (input.files && input.files[0]) {
        this.imgName = input.files[0].name;
      } else {
        this.errMsg = "Please upload an image";
      }
    }
  }
};

exports.courseFormErrs = {
  methods: {
    checkForm(e) {
      Object.keys(this.course).map(key => {
        if (key == "tags" && this.course.tags.length < 1) {
          this.$set(this.formErrors, "tags", "Please select at least one tag");
        }
        if (this.course[key] == "") {
          this.$set(this.formErrors, key, "This field is required");
        }
      });

      if (Object.keys(this.formErrors).length > 0) {
        return e.preventDefault();
      }
    }
  }
};



