<template>
  <form class="form" method="POST" action="/login" @submit="checkForm">
      <input type="hidden" name="_token" :value="this.$root.token">
      <h1>Login</h1>
      <div class="field">
        <label class="label" for="email">E-Mail</label>
        <div class="control has-icons-left">
          <input class="input" :class="{ 'is-danger': (formErrors['email'] || formErrors['credentials']) }"  id="email" ref="email" name="email" type="email" placeholder="Email" v-model.trim="login.email" @focus="clearError('email'); clearError('credentials')" required>
          <span class="icon is-small is-left">
            <i class="fas fa-envelope"></i>
          </span>
        </div>
        <p v-if="formErrors.credentials" class="help is-danger">{{ formErrors.credentials }}</p>
        <p v-if="formErrors.email" class="help is-danger">{{ formErrors.email }}</p>
      </div>
      
      <div class="field">
        <label class="label" for="password">Password</label>
        <div class="control has-icons-left">
          <input class="input" :class="{ 'is-danger': formErrors['password'] }" id="password" name="password" type="password" v-model.trim="login.password" placeholder="Password" @focus="clearError('password')" required>
          <span class="icon is-small is-left">
            <i class="fas fa-lock"></i>
          </span>
        </div>
        <p v-if="formErrors.password" class="help is-danger">{{ formErrors.password }}</p>
      </div>

      <div class="field flex">
        <label class="checkbox">
          <input type="checkbox" name="remember" :checked="login.remember == 'on'"> Remember Me
        </label>
        <a class="pass_link" href="/password/reset">Forgot Your Password?</a>
      </div>

      <div class="field">
        <div class="control">
          <button class="button is-primary" type="submit">Login</button>
        </div>
      </div>
    </form>
</template>

<script>
import { deleteErrors } from "../mixins";

export default {
  name: "Login",
  props: ["old", "errors"],
  mixins: [deleteErrors],
  data() {
    return {
      login: {
        email: "",
        password: "",
        remember: "off"
      },
      formErrors: {}
    };
  },
  methods: {
    checkForm(e) {
      Object.keys(this.login).map(key => {
        if (this.login[key] == "" && key != "remember") {
          console.log(key);
          this.$set(this.formErrors, key, "This field is required");
        }
      });
      const emailRegEx = /\S+@\S+\.\S+/;
      if (this.login.email.length < 6 || !this.checkEmail(this.login.email)) {
        this.$set(
          this.formErrors,
          "email",
          "Please enter a valid email address"
        );
      }

      if (Object.keys(this.formErrors).length > 0) {
        return e.preventDefault();
      }
    },
    checkEmail(email) {
      const emailRegEx = /\S+@\S+\.\S+/;
      return emailRegEx.test(email);
    }
  },
  mounted() {
    this.$nextTick(() => {
      if (
        !Array.isArray(this.old) &&
        Object.getOwnPropertyNames(this.old).length > 0
      ) {
        this.login = Object.assign({}, this.login, this.old);
      }

      if (this.errors.length) {
        //let errObj = {};
        this.errors.map(err => {
          let name = err.split(" ")[1];
          // errObj[name] = err;
          this.$set(this.formErrors, name, err);
        });
        //this.formErrors = Object.assign({}, this.formErrors, errObj);
      }

      this.$refs.email.focus();
    });
  }
};
</script>



