<template>
  <div class="course_review">
    <div class="review_body">
      <div class="review_header">
        <div v-if="!editMode" class="review_user">
          <img class="gravatar" :src="comment.gravatar" alt="User Gravitar">
          <p>{{ comment.username }}</p>
        </div>
        <div v-else class="review_edit_msg">
          <p>Please update your comment</p>
        </div>
        <div v-if="!editMode" class="review_rating">{{ comment.rating | stars }}</div>
        <div v-else class="review_rating">
          <star-component :default="comment.rating" @selected="updateRating"></star-component>
        </div>
        <div v-if="!editMode" class="review_time">{{ comment.updated_at }}</div>
      </div>
      <div class="user_review">
        <p v-if="!editMode">{{ comment.comment }}</p>
        <div v-else>
          <div class="field">
            <div class="control">
              <textarea class="textarea" name="comment" id="comment" v-model.trim="userComment" @focus="clearErrors"></textarea>
            </div>
          </div>
          <div v-if="commentErrs.length">
            <p v-for="(err,index) in commentErrs" :key="index" class="help is-danger">{{ err }}</p>
          </div>
          <div class="field is-grouped is-grouped-right">
            <div class="control">
              <a class="button is-primary" @click="updateMsg">Save</a>
            </div>
            <div class="control" @click="editMode = false">
              <a class="button is-light">Cancel</a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="review_edit">
      <div v-if="comment.canEdit && !editMode">
        <div @click="editMode = true" aria-label="Edit Review"><i aria-hidden="true" class="fas fa-pencil-alt fa-lg has-text-link"></i></div>
        <div @click="confirmModal = true" aria-label="Delete Reivew"><i aria-hidden="true" class="fas fa-trash-alt fa-lg has-text-danger"></i></div>
      </div>
    </div>
    <transition name="fade">
      <div v-if="confirmModal" class="review_delete modal is-active">
        <div class="modal-background"></div>
        <div class="modal-content">
          <p class="has-text-centered">Are you sure you want to delete your comment?</p>
          <div class="field is-grouped is-grouped-centered">
            <div class="control">
              <a class="button is-danger" @click="deleteMsg">Delete Message</a>
            </div>
            <div class="control" @click="confirmModal = false">
              <a class="button is-light">Cancel</a>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import StarComponent from "./Star";
import { commentErrors } from "../../mixins";

export default {
  name: "Comment",
  props: ["comment"],
  mixins: [commentErrors],
  components: {
    StarComponent
  },
  data() {
    return {
      editMode: false,
      userComment: "",
      rating: 0,
      commentErrs: [],
      confirmModal: false
    };
  },
  filters: {
    stars(num) {
      return "★".repeat(num) + "☆".repeat(5 - num);
    }
  },
  methods: {
    updateMsg() {
      if (this.userComment === "" || this.userComment.length < 7) {
        this.commentErrs.push(
          "The comment field is required, with at least 7 characters"
        );
      }

      if (this.rating < 1 || this.rating > 5) {
        this.commentErrs.push("A star rating of between 1 and 5 is required");
      }

      if (!this.commentErrs.length) {
        axios
          .post("/api/courses/comments/update", {
            comment_id: this.comment.id,
            comment: this.userComment,
            rating: this.rating
          })
          .then(res => {
            this.$eventBus.$emit("newcomments", res.data);
            this.$eventBus.$emit("msg", {
              status: "is-success",
              header: "Success",
              message: "Your message has been updated"
            });
            this.editMode = false;
          })
          .catch(err => {
            console.warn(err);
          });
      }
    },
    deleteMsg() {
      axios
        .post("/api/courses/comments/delete", {
          comment_id: this.comment.id
        })
        .then(res => {
          location.reload();
        })
        .catch(err => {
          console.warn(err);
        });
    }
  },
  mounted() {
    this.userComment = this.comment.comment;
    this.rating = this.comment.rating;
  }
};
</script>




