<template>
  <div>
    <div v-if="!msgPosted">
      <form method="POST" :action="`/courses/${courseid}/comments`" @submit.prevent="submitReview">
        <h3>Enter a Review</h3>
        <div>
          <div class="field">
            <label class="label" for="comment">Comment</label>
            <div v-if="commentErrs.length">
              <p v-for="(err,index) in commentErrs" :key="index" class="help is-danger">{{ err }}</p>
            </div>
            <div class="control">
              <textarea class="textarea" name="comment" id="comment" placeholder="If you liked the course, please leave a review..." v-model.trim="comment" @focus="clearErrors"></textarea>
            </div>
          </div>
          <div class="form_footer">
            <star-component :default="0" @selected="updateRating"></star-component>
            <button class="button is-primary" type="submit">Submit ➡</button>
          </div>
        </div>
      </form>
    </div>
    <h3 v-else><em>Thank you for your comment!</em></h3>
  </div>
</template>

<script>
import StarComponent from "./Star";
import { commentErrors } from "../../mixins";

export default {
  name: "AddComment",
  props: ["courseid", "errors"],
  mixins: [commentErrors],
  components: {
    StarComponent
  },
  data() {
    return {
      comment: "",
      rating: 0,
      commentErrs: [],
      msgPosted: false
    };
  },
  methods: {
    submitReview() {
      if (this.comment === "" || this.comment.length < 7) {
        this.commentErrs.push(
          "The comment field is required, with at least 7 characters"
        );
      }

      if (this.rating < 1 || this.rating > 5) {
        this.commentErrs.push("A star rating of between 1 and 5 is required");
      }

      if (!this.commentErrs.length) {
        //post and fetch data
        axios
          .post("/api/courses/" + this.courseid + "/comments", {
            comment: this.comment,
            rating: this.rating
          })
          .then(res => {
            if (res.data.length) {
              this.comment = "";
              this.$eventBus.$emit("newcomments", res.data);
              this.$eventBus.$emit("msg", {
                status: "is-success",
                header: "Success",
                message: "Your message has been added"
              });
              this.msgPosted = true;
            }
          })
          .catch(err => {
            console.warn(err);
          });
      }
    }
  },
  mounted() {
    if (this.errors.length) {
      this.commentErrs = this.errors;
    }
  }
};
</script>

<style lang="scss" scoped>
.field,
.label {
  margin-bottom: 0;
}

.field {
  .help {
    margin: 0;
  }
}
</style>





