<template>
  <form class="form" method="POST" :action="`/courses/${current.slug}/update`" enctype="multipart/form-data" @submit="checkForm">
    <h1>Update: {{course.name}}</h1>
    <input type="hidden" name="_token" :value="this.$root.token">

    <div class="field">
      <label class="label" for="name">Course Name</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['name'] }" id="name" name="name" type="text" placeholder="Please enter the course name" v-model.trim="course.name" @click="clearError('name'); clearError('course')" required>
        <span class="icon is-small is-left">
          <i class="fas fa-golf-ball"></i>
        </span>
      </div>
      <p v-if="formErrors.name" class="help is-danger">{{ formErrors.name }}</p>
      <p v-if="formErrors.course" class="help is-danger">{{ formErrors.course }}</p>
    </div>

    <div class="field">
      <label class="label" for="desc">Description</label>
      <div class="control">
        <textarea class="textarea" :class="{ 'is-danger': formErrors['description'] }" name="description" id="desc" placeholder="Please Enter A Brief Description" v-model.trim="course.description" @focus="clearError('description')" required></textarea>
      </div>
      <p v-if="formErrors.description" class="help is-danger">{{ formErrors.description }}</p>
    </div>

    <p class="input_label">Image</p>
    <div class="img_container">
      <figure class="image is-2by1">
        <img :src="`/storage/courses/${current.img_src}`" :alt="`Current ${course.name} image`">
      </figure>
    </div>
    <div class="field">
      <div class="file has-name is-fullwidth">
        <label class="file-label">
          <input class="file-input" type="file" name="image" accept="image/*" id="file" ref="image" @change="updateImg">
          <span class="file-cta">
            <span class="file-icon">
              <i class="fas fa-upload"></i>
            </span>
            <span class="file-label">
              Choose a new image if required…
            </span>
          </span>
          <span class="file-name" v-if="imgName">
            {{ imgName }}
          </span>
        </label>
      </div>
      <p v-if="formErrors.image" class="help is-danger">{{ formErrors.image }}</p>
    </div>

    <div class="field">
      <label class="label" for="address">Address</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['address'] }" id="address" name="address" type="text" @focus="clearError('address')" v-model.trim="course.address" required>
        <span class="icon is-small is-left">
          <i class="fas fa-home"></i>
        </span>
      </div>
      <p v-if="formErrors.address" class="help is-danger">{{ formErrors.address }}</p>
    </div>
    
    <div class="field">
      <label class="label" for="lat">Latitude</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['lat'] }" id="lat" name="lat" type="text" @focus="clearError('lat')" v-model.trim="course.lat" required>
        <span class="icon is-small is-left">
          <i class="fas fa-location-arrow"></i>
        </span>
      </div>
      <p v-if="formErrors.lat" class="help is-danger">{{ formErrors.lat }}</p>
    </div>
    
    <div class="field">
      <label class="label" for="lng">Longitude</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['lng'] }" id="lng" name="lng" type="text" @focus="clearError('lng')" v-model.trim="course.lng" required>
        <span class="icon is-small is-left">
          <i class="fas fa-location-arrow"></i>
        </span>
      </div>
      <p v-if="formErrors.lng" class="help is-danger">{{ formErrors.lng }}</p>
    </div>
    
    <div class="field">
      <label class="label" for="tel">Tel No.</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['tel_no'] }" id="tel" name="tel_no" type="text" @focus="clearError('tel_no')" v-model.trim="course.tel_no" required>
        <span class="icon is-small is-left">
          <i class="fas fa-phone"></i>
        </span>
      </div>
      <p v-if="formErrors.tel_no" class="help is-danger">{{ formErrors.tel_no }}</p>
    </div>
    
    <div class="field">
      <label class="label" for="website">Website URL</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['website'] }" id="website" name="website" type="text" placeholder="www.domain.co.uk" @focus="clearError('website')" v-model.trim="course.website" required>
        <span class="icon is-small is-left">
          <i class="fas fa-external-link-alt"></i>
        </span>
      </div>
      <p v-if="formErrors.website" class="help is-danger">{{ formErrors.website }}</p>
    </div>

    <p class="input_label">Tags</p>
    <div class="field is-grouped form_tags" v-if="tags">
      <p role="group" aria-label="Course tags to ammend" class="control" v-for="tag in tags" :key="tag.id">
        <label class="checkbox">
          <input type="checkbox" :id="tag.id" name="tags[]" :value="tag.id" @click="clearError('tags')" v-model="course.tags">
          {{ tag.tag_name }}
        </label>
      </p>
      <p v-if="formErrors.tags" class="help is-danger">{{ formErrors.tags }}</p>
    </div>

    <div class="field">
      <label class="label" for="weekCost">WeekDay Cost</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['weekday_cost'] }" id="weekCost" name="weekday_cost" type="text" placeholder="20.50" @focus="clearError('weekday_cost')" v-model.trim="course.weekday_cost" required>
        <span class="icon is-small is-left">
          <i class="fas fa-pound-sign"></i>
        </span>
      </div>
      <p v-if="formErrors.weekday_cost" class="help is-danger">{{ formErrors.weekday_cost }}</p>
    </div>
    
    <div class="field">
      <label class="label" for="weekendCost">Weekend Cost</label>
      <div class="control has-icons-left">
        <input class="input" :class="{ 'is-danger': formErrors['weekendCost'] }" id="weekendCost" name="weekend_cost" type="text" placeholder="30.50" @focus="clearError('weekend_cost')" v-model.trim="course.weekend_cost" required>
        <span class="icon is-small is-left">
          <i class="fas fa-pound-sign"></i>
        </span>
      </div>
      <p v-if="formErrors.weekend_cost" class="help is-danger">{{ formErrors.weekend_cost }}</p>
    </div>

    <div class="field">
      <div class="control">
        <button class="button is-primary" type="submit">Update →</button>
      </div>
    </div>
  </form>
</template>

<script>
import { deleteErrors } from "../mixins";
import { updateImage } from "../mixins";
import { courseFormErrs } from "../mixins";

export default {
  name: "UpdateCourse",
  props: ["current", "currentTags", "tags", "old", "errors"],
  mixins: [deleteErrors, updateImage, courseFormErrs],
  data() {
    return {
      course: {
        name: "",
        description: "",
        address: "",
        tel_no: "",
        lat: "",
        lng: "",
        tags: [],
        website: "",
        weekday_cost: "",
        weekend_cost: ""
      },
      formErrors: {},
      errMsg: "",
      imgName: ""
    };
  },
  mounted() {
    if (
      !Array.isArray(this.old) &&
      Object.getOwnPropertyNames(this.old).length > 0
    ) {
      this.course = Object.assign({}, this.course, this.old);
    } else {
      this.course = Object.assign({}, this.course, this.current);
      this.course.tags = this.currentTags;
    }

    if (this.errors.length) {
      this.errors.map(err => {
        let name = err.split(" ")[1];
        this.$set(this.formErrors, name, err);
      });
    }
  }
};
</script>



